body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  font-size: 9px;
  color: #fff;
}


.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

.container {
  position: relative;
  width: 300px;
  height: 300px;
  -webkit-transition: -webkit-perspective 300ms ease;
  transition: -webkit-perspective 300ms ease;
  transition: perspective 300ms ease;
  transition: perspective 300ms ease, -webkit-perspective 300ms ease;
}

.cube {
  height: 100%;
  width: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
  -webkit-transition-duration: 100ms;
          transition-duration: 100ms;
}

.cube figure {
  position: absolute;
  margin: 0;
  height: 300px;
  width: 300px;
  display: block;
  opacity: 0.2;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
  border: #979797 solid 0.1rem;
  background: #fff;
  -webkit-animation-name: color_change;
          animation-name: color_change;
	-webkit-animation-duration: 5s;
	        animation-duration: 5s;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
}

.cube figure:nth-of-type(1) {
  -webkit-transform: translateZ(150px);
          transform: translateZ(150px);
}
.cube figure:nth-of-type(2) {
  -webkit-transform: rotateX(180deg) translateZ(150px);
          transform: rotateX(180deg) translateZ(150px);
}
.cube figure:nth-of-type(3) {

  -webkit-transform: rotateY(-90deg) translateZ(150px);

          transform: rotateY(-90deg) translateZ(150px);
}
.cube figure:nth-of-type(4) {


  -webkit-transform: rotateY(90deg) translateZ(150px);


          transform: rotateY(90deg) translateZ(150px);
}
.cube figure:nth-of-type(5) {

  -webkit-transform: rotateX(90deg) translateZ(150px);

          transform: rotateX(90deg) translateZ(150px);
}
.cube figure:nth-of-type(6) {
  -webkit-transform: rotateX(-90deg) translateZ(150px);
          transform: rotateX(-90deg) translateZ(150px);
}

@-webkit-keyframes animateCube {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

@keyframes animateCube {
  0% {
    -webkit-transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
            transform: translateZ(-150px) rotateX(-180deg) rotateY(-180deg);
  }
  100% {
    -webkit-transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
            transform: translateZ(-150px) rotateX(180deg) rotateY(180deg);
  }
}

@-webkit-keyframes color_change {
  0% { box-shadow: inset 10px 10px 87px 89px #F72442; }
  25% { box-shadow: inset 10px 10px 87px 89px #50E3C2; }
  50% { box-shadow: inset 10px 10px 87px 89px #7ED321; }
  75% { box-shadow: inset 10px 10px 87px 89px black; }
  100% { box-shadow: inset 10px 10px 87px 89px red; }

}
@keyframes color_change {
  0% { box-shadow: inset 10px 10px 87px 89px #F72442; }
  25% { box-shadow: inset 10px 10px 87px 89px #50E3C2; }
  50% { box-shadow: inset 10px 10px 87px 89px #7ED321; }
  75% { box-shadow: inset 10px 10px 87px 89px black; }
  100% { box-shadow: inset 10px 10px 87px 89px red; }
}


